/*! fabian.buechler.io/foto/ */

/* Normalize */

@import '../node_modules/normalize.css/normalize.css';

@import 'colors_fonts';
@import 'foto_colors';
@import 'icons';
@import 'basic';

/* Basic Layout. */

html {
    height: 100%;
}

body {
    background-color: $dark;
    color: $light;
}

a {
    color: $highlight;

    &:hover,
    &:focus,
    &:active {
        background-color: $medium;
        color: light;
    }
}

/* Main Layout. */

.site-header {
    margin: 1rem;

    a {
        text-decoration: none;
    }
}

.main {
    margin: 1rem;
}

.footer {
    background-color: $medium;
    padding: 1rem;

    p {
        margin: 0;
    }
}

/* Foto user-list, albums-list and album grid. */

.foto {

    .icon-list {
        display: grid;
        grid-gap: 1rem;
        grid-template-columns: repeat(3, 1fr);

        .item {
            font-size: 1.2rem;
        }

        .icon {
            display: block;
            line-height: 2rem;
            text-decoration: none;

            &:before {
                color: white;
                font-size: 2rem;
                line-height: 2rem;
                margin: 0.5rem 1rem 0.5rem 0.5rem;
            }
        }

    }

    .header {
        margin-bottom: 2rem;

        .title {
            color: $medium;
            margin-bottom: 0;
        }
        .date {
            font-weight: bold;
        }
    }

    .description {
        color: $gray-pastel;
    }

    .list {
        list-style: none  none;
        padding-left: 0;
    }

    .grid {
        list-style: none;
        display: grid;
        grid-gap: 1rem;
        grid-template-columns: minmax(110px, auto);
        grid-auto-rows: minmax(110px, auto);

        a {
            background-color: transparent;
            line-height: 0;
            outline: 3px solid transparent;
            position: relative;

            &:hover,
            &:focus,
            &:active {
                outline-color: $highlight;
            }
        }

        img {
            height: 100%;
            width: 100%;
        }

        .meta {
            background-color: rgba($medium, 0.7);
            color: $light;
            line-height: 1;
            bottom: 0;
            overflow-wrap: break-word;
            hyphens: auto;
            position: absolute;
            width: 100%;

            .title {
                display: block;
                padding: 0.3rem 0.3rem 0 0.3rem;
            }

            .date {
                font-weight: bold;
                font-size: 0.9rem;
                display: block;
                padding: 0 0.3rem 0.3rem 0.3rem;
            }
        }

        // 1 x 110 @ 142
        @media screen and (min-width: 142px) {
            grid-template-columns: repeat(1, minmax(110px, auto));
        }
        // 2 x 110 @ 268
        @media screen and (min-width: 268px) {
            grid-template-columns: repeat(2, minmax(110px, auto));
        }
        // 3 x 165 @ 559
        @media screen and (min-width: 559px) {
            grid-template-columns: repeat(3, minmax(165px, auto));
        }
        // 4 x 165 @ 740
        @media screen and (min-width: 740px) {
            grid-template-columns: repeat(4, minmax(165px, auto));
        }
        // 5 x 220 @ 1196
        @media screen and (min-width: 1196px) {
            grid-template-columns: repeat(5, minmax(220px, auto));
        }
        // 6 x 220 @ 1432
        @media screen and (min-width: 1432px) {
            grid-template-columns: repeat(6, minmax(220px, auto));
        }
        // 7 x 220 @ 1668
        @media screen and (min-width: 1668px) {
            grid-template-columns: repeat(7, minmax(220px, auto));
        }
        // 8 x 220 @ 1904
        @media screen and (min-width: 1904px) {
            grid-template-columns: repeat(8, minmax(220px, auto));
        }
        // 9 x 220 @ 2140
        @media screen and (min-width: 2140px) {
            grid-template-columns: repeat(9, minmax(220px, auto));
        }
        // 10 x 220 @ 2376
        @media screen and (min-width: 2376px) {
            grid-template-columns: repeat(10, minmax(220px, auto));
        }
        // 11 x 220 @ 2612
        @media screen and (min-width: 2612px) {
            grid-template-columns: repeat(11, minmax(220px, auto));
        }
    }
}

/* Photoswipe */

$pswp__assets-path: '../node_modules/photoswipe/src/css/default-skin/';
@import '../node_modules/photoswipe/src/css/main.scss';
@import '../node_modules/photoswipe/src/css/default-skin/default-skin.scss';



